.comment {
  font-size: 1.3rem;
  &--child {
    margin-left: 1rem; }
  &__header {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
    grid-gap: .5rem; }
  &__create-at {
    font-size: 1rem;
    color: $color-gray; }
  &__author {
    font-size: 1rem;
    text-align: center;
    font-weight: bold; }
  &__reply {
    text-align: right;
    &-img {
      width: 3rem; } }
  &__line {
    display: block;
    margin: 0;
    padding: 0;
    border: initial;
    border-top: 1px solid $color-orange; }
  &__content {
    margin: 1rem 0; } }

